<template>
<div id="settlement_record">
    <div class="tits">
      <span class="jies">提款记录</span>
      <div class="content_jies">&nbsp;</div>
       <DatePicker type="daterange" :value="time" split-panels placeholder="指定提款时间" style="width: 240px" @on-change="datechange($event)"></DatePicker>
    </div>
    <div class="shuj_bnox">
    	<div class="shuj_j_npx">
    	<div><img src="/static/icon/8.png"><span>{{Drawing_statistics.total_success_money}}</span><br><span>成功金额</span></div>
    	<div><img src="/static/icon/11.png"><span>{{Drawing_statistics.total_success_fee}}</span><br><span>手续费</span></div>
    	<div><img src="/static/icon/12.png"><span>{{Drawing_statistics.total_success_count}}</span><br><span>成功笔数</span></div>
    	</div>
    </div>
    <div class="store_bottom">
      <table>
        <thead>
          <tr>
          <th>提款信息</th>
          <th>申请时间</th>
          <th>申请金额</th>
          <th>手续费</th>
          <th>处理时间</th>
          <th>处理结果</th>
          </tr>
        </thead>
      <tbody data-v-9154b70e="">
              <td v-if="list.length == 0" colspan="11" align="center">
              <div class="none_now" >
              <img src="/static/icon/7.png">
              <div>暂无相关数据</div>
              </div>
              </td>
              <tr data-v-9154b70e="" v-else v-for="(item,index) in list" :key="item.Id">
              <td data-v-9154b70e="">{{item.bank.name}}-{{item.bank.bank_type}}</td>
              <td data-v-9154b70e="">{{item.sub_time}}</td>
              <td data-v-9154b70e="">{{item.money}}</td>
              <td data-v-9154b70e="">{{item.service}}</td>
              <td data-v-9154b70e="">{{item.com_time}}</td>
              <td data-v-9154b70e="" v-if="item.status == 1" style="color:rgb(42, 98, 255);"><div class="qiu" style="background: rgb(42, 98, 255);"></div>处理成功</td></td>
              <td data-v-9154b70e="" v-if="item.status == 2" style="color: rgb(239, 134, 44);"><div class="qiu" style="background: rgb(243, 149, 44);"></div>正在处理</td>
              <td data-v-9154b70e="" v-if="item.status == 3" style="color: rgb(232, 72, 11);"><div class="qiu" style="background: rgb(232, 72, 11);"></div>提现退款</td>
              </tr>
      </tbody>
      </table>
      <Page :total="page.total"  :page-size="15" show-total :current="page.current" class="fenye" v-if="list.length != 0" @on-change="ajaxlist($event,time)" />
    </div>
    <Spin fix v-if="spin"></Spin>
</div>
</template>
<script>
export default {
         data(){
            return {
               spin:true,
               Drawing_statistics:"",
               time:'',
               list:[],
               page:{
                 current:1,
                 total:''
               }
            }
         },
         mounted(){
            this.time = [this.getDay(0),this.getDay(1)];
            this.ajaxlist(1,this.time);
         },
         methods: {
           getDay(num) {
               var today = new Date();
               var nowTime = today.getTime();
               var ms = 24*3600*1000*num;
               today.setTime(parseInt(nowTime + ms));
               var oYear = today.getFullYear();
               var oMoth = (today.getMonth() + 1).toString();
               if (oMoth.length <= 1) oMoth = '0' + oMoth;
               var oDay = today.getDate().toString();
               if (oDay.length <= 1) oDay = '0' + oDay;
               return oYear+'-'+oMoth+'-'+oDay;
           },
           ajaxlist(page,time){
            this.spin = true;
            this.$http.post('api/user/settlement_record',{page:page,time:time}, res => {
                  this.spin = false;
                 if (res.status == 200) {
                     if(res.data.code == 200){
                      this.list = res.data.data.list;
                      this.Drawing_statistics = res.data.data.Drawing_statistics;
                      this.page.current = res.data.data.current_page;
                      this.page.total = res.data.data.total;
                     }else if(res.data.code == 201){
                       this.$Message.warning(res.data.msg);
                     }
                 } else {
                    console.log(res.message);
                 }
            });
           },
           datechange(n){
               this.time = n;
               this.ajaxlist(1,this.time);
           }
         }
    }
</script>



